.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*Home*/
h1{
  font: 700 40px / 1 arial;
  text-transform: uppercase;
  color: #fbcc46;
  padding-bottom: 0;
  /* padding-top: 3%; */
  text-align: center;
  /* font-family: 'Acme'; */
}
@keyframes typing-p{
  0%{
     width: 0;
  }
  100%{
     width: 100%;
  }
}
p{
  display: inline-block;
  font-size: 33px;
   color: white;
   padding: 6%;
   margin-bottom: 0;
   padding-top: 0;
   padding-bottom: 0%;
   width: 50%;
}
#astronaut img{
  width: 350px;
  margin: 0;
  padding: 0;
}
#astronaut{
  display: inline-block;
  text-align: center;
  width: 35%;
}
#contenedor{
  padding: 0;
  margin: 0;
}
@media (max-width: 800px) {
  h1{
     font-size: 35px;
  }
  p{
     width: auto;
     font-size: 28px;
 }
 #astronaut{
 width:100%
 }
}